import React from "react";
import { Box, Typography, Button } from "@mui/material";
import Banner_img from "../assets/img/banner.png";

const Banner = () => {
  return (
    <Box
      sx={{ mt: { lg: "212px", xs: "70px" }, ml: { sm: "50px" } }}
      position="relative"
      p="20px"
    >
      <Typography color="#FF2625" fontWeight="600" fontSize="26px">
        Fitness Tracker
      </Typography>
      <Typography
        fontWeight={700}
        sx={{ fontSize: { lg: "44px", xs: "40px" } }}
        mb="23px"
        mt="30px"
      >
        Sweat, Smile <br /> and Repeat <br />
      </Typography>
      <Typography fontSize="22px" lineHeight="35px" mb={3}>
        Check out the best effective exercises tracker
      </Typography>
      <Button
        variant="contained"
        color="error"
        herf="#exercises"
        sx={{ background: "#ff2625", padding: "10 px" }}
      >
        Explore Exercises
      </Button>
      <Typography
        fontWeight={600}
        color="#ff2625"
        sx={{ opacity: 0.1, display: { lg: "block", xs: "none" } }}
        fontSize="200px "
      >
        Workout
      </Typography>
      <img src={Banner_img} alt="banner" className="hero-banner-img" />
    </Box>
  );
};

export default Banner;
